<script>
    export let System = {
        pages: {
            current: undefined,
            list: [],
            add: function (name, svelte) {
                let page = {
                    name: name,
                    svelte: svelte,
                };
                this.list.push(page);
            },
            change: function (name) {
                for (const page of this.list) {
                    if (page.name == name) {
                        System.pages.current = page.svelte;
                    }
                }
                return undefined;
            },
        },
        getDeviceType: function () {
            const ua = navigator.userAgent;
            if (/(tablet|ipad|playbook|silk)|(android(?!.*mobi))/i.test(ua)) {
                return "tablet";
            }
            if (
                /Mobile|iP(hone|od)|Android|BlackBerry|IEMobile|Kindle|Silk-Accelerated|(hpw|web)OS|Opera M(obi|ini)/.test(
                    ua,
                )
            ) {
                return "mobile";
            }
            return "desktop";
        },
        projects: {
            list: [],
            view: undefined,
        },
        technos: {
            list: [],
            new: function (name, link) {
                let o = {
                    name: name,
                    link: link,
                };
                this.list.push(o);
            },
            getByName: function (name) {
                for (const o of this.list) {
                    if (name == o.name) {
                        return o;
                    }
                }
                return undefined;
            },
        },
        categories: {
            list: [],
        },
    };

    System.technos.new(
        "Javascript",
        "https://developer.mozilla.org/fr/docs/Web/JavaScript",
    );
    System.technos.new("Svelte", "https://svelte.dev/");
    System.technos.new("Python", "https://www.python.org/");
    System.technos.new("C++", "https://fr.wikipedia.org/wiki/C%2B%2B");
    System.technos.new("QT", "https://wiki.qt.io/About_Qt");

    import * as apps from "./Apps";
    for (const a of Object.keys(apps)) {
        const app = new apps[a]();

        for (const category of app.categories) {
            if (!System.categories.list.includes(category)) {
                System.categories.list.push(category);
            }
        }

        System.projects.list.push(app);
    }

    import Projects from "./Projects.svelte";
    System.pages.add("Projects", Projects);

    import Presentation from "./Presentation.svelte";
    System.pages.add("Presentation", Presentation);

    import Contacts from "./Contacts.svelte";
    System.pages.add("Contacts", Contacts);

    import Taskbar from "./Taskbar.svelte";

    System.pages.change("Projects");
</script>

<div id="html">
    {#if window.innerWidth >= 1000 && window.innerHeight >= 500}
        <svelte:component this={Taskbar} {System} />
        <div id="body">
            <svelte:component this={System.pages.current} {System} />
        </div>
    {/if}
</div>

