<script>
    import View from "./View.svelte";
    import Dropdown from "./Dropdown.svelte";
    import App from "./App.svelte";
    export let System;

    let appList = [];
    let sortType = "Nom";

    let technoList = ["Tous"];
    for (const techno of System.technos.list) {
        technoList.push(techno.name);
    }
    let technoSelected = technoList[0];

    let categoryList = ["Toutes"];
    for (const category of System.categories.list) {
        categoryList.push(category);
    }
    let categorySelected = categoryList[0];

    function filter() {
        let array = [];

        for (const project of System.projects.list) {
            if (
                (categorySelected == "Toutes" ||
                    project.categories.includes(categorySelected)) &&
                (technoSelected == "Tous" ||
                    project.technos.includes(technoSelected))
            ) {
                array.push(project);
            }
        }

        array = sort(array);

        appList = array;
    }

    function sort(array) {
        switch (sortType) {
            case "Date de dbut":
                for (let i = 0; i < array.length; i++) {
                    let j = i;
                    while (j > 0 && array[j].dateStart < array[j - 1].dateStart) {
                        let swap = array[j - 1];
                        array[j - 1] = array[j];
                        array[j] = swap;
                        j--;
                    }
                }
                break;
            case "Date de fin":
                for (let i = 0; i < array.length; i++) {
                    let j = i;
                    while (j > 0 && array[j].dateEnd < array[j - 1].dateEnd) {
                        let swap = array[j - 1];
                        array[j - 1] = array[j];
                        array[j] = swap;
                        j--;
                    }
                }
                break;
        }
        return array;
    }

    filter();
</script>

{#if System.projects.view != undefined}
    <div id="view">
        <svelte:component this={View} {System} app={System.projects.view} />
    </div>
{:else}
    <div id="header">
        <div class="label">
            {appList.length} projet{#if appList.length > 1}s{/if}
        </div>

        <div id="drop">
            <div class="label">Trier par</div>
            <svelte:component
                this={Dropdown}
                array={["Nom", "Date de dbut", "Date de fin"]}
                selected={sortType}
                selecting={function (element) {
                    sortType = element;
                    filter();
                }}
            />
        </div>

        <div id="drop">
            <div class="label">Catgories</div>
            <svelte:component
                this={Dropdown}
                array={categoryList}
                selected={categorySelected}
                selecting={function (element) {
                    categorySelected = element;
                    filter();
                }}
            />
        </div>

        <div id="drop">
            <div class="label">Langages</div>
            <svelte:component
                this={Dropdown}
                array={technoList}
                selected={technoSelected}
                selecting={function (element) {
                    technoSelected = element;
                    filter();
                }}
            />
        </div>
    </div>
    <div id="body">
        <br />
        <br />
        <br />
        <br />
        <div id="container">
            {#each appList as app}
                <svelte:component this={App} {System} {app} />
            {/each}
        </div>
        <br />
        <br />
        <br />
        <br />
    </div>
{/if}

<style>
    #body {
        overflow-y: scroll;
        scrollbar-color: transparent transparent;
        height: 100%;
        padding-bottom: 10px;
    }

    #header {
        text-align: center;
        position: fixed;
        left: 0;
        top: 10vh;
        width: 100%;
        height: 5vh;
        z-index: 1;
        padding: 1%;
        color: white;
        background: rgba(0, 0, 100, 0.8);
        display: grid;
        grid-template-columns: repeat(4, 1fr);
    }

    #drop {
        display: flex;
    }

    .label {
        transform: translate(0, 25%);
        padding-right: 1vw;
    }

    #container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        width: 98%;
        column-gap: 5vw;
        row-gap: 5vh;
    }

    #view {
        border: solid;
        background: #6cb4ee;
        height: 75vh;
        padding: 1%;
    }
</style>
